<template>
    <div>
        <div class="search">
            <input v-model="keyword" class="search-input" type="text" placeholder="输入城市名或拼音"/>
        </div>
        <div class="search-content" ref="search" v-show="keyword">
            <ul>
                <li class="search-item border-bottom" v-for="item in list" :key="item.id" @click="handleCityClick(item.name)">{{item.name}}</li>
                <li class="search-item border-bottom" v-show="hasNoData">没有找到匹配数据</li class="search-item border-bottom">
            </ul>
        </div>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex';
    import Bscroll from 'better-scroll';
    export default {
        name: 'CitySearch',
        props:{
            cities: Object
        },
        data() {
            return {
                keyword: '',
                list: [],
                timer: null
            }
        },
        computed: {
            hasNoData(){
                return !this.list.length
            }
        },
        watch: {
            keyword () {
            if (this.timer) {
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
                if (this.keyword === '' || !this.keyword) {
                this.list = []
                return
                }
                const result = []
                for (let i in this.cities) {
                this.cities[i].forEach((value) => {
                    if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
                    result.push(value)
                    }
                })
                }
                this.list = result
            }, 100)
            }
        },
        methods: {
            handleCityClick(city){
                this.changeCity(city)
                this.$router.push('/')
            },
            ...mapMutations(['changeCity'])
        },
        mounted() {
            this.scroll = new Bscroll(this.$refs.search)
        }, 
    }
</script>

<style lang="stylus" scoped>
@import './style/Search.styl';
</style>


